.load {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

button {
  background: black;
  color: white;
  font-family: "Times New Roman";
  border: none;
  padding: 5px;
  cursor: pointer;
}

.title {
  font-size: 100px;
  background: black;
  color: white;
  font-family: "Times New Roman";
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
}

.demotivatorContainer {
  display: flex;
  justify-content: center;
  height: calc(100vh - 60px);
  align-items: center;
}

.inputBack {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  .inputContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;

    textarea {
      margin-bottom: 10px;
      height: 50%;
      width: 100%;
      resize: none;

      &:focus {
        border: none;
        outline: 1px solid black;
      }
    }
  }
}

.buttonContainer {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}