.demotivator {
  background: black;
  padding: 50px 100px 30px 100px;

  .imgContainer {
    border: 2px solid white;
    padding: 4px;
    display: grid;

    img {
      min-width: 500px;
      height: 100%;
    }
  }

  .textContainer {
    font-size: 100px;
    color: white;
    display: flex;
    justify-content: center;
    font-family: "Times New Roman";
  }

  .secondTextContainer {
    font-size: 50px;
    color: white;
    display: flex;
    justify-content: center;
    font-family: "Times New Roman";
  }
}